<template>
	<div class="accountQue">   
		<div class="box">
			<div class="group" v-for="(item,index) in conList" :key="index">
				<div class="flex headline" @click="item.tab = !item.tab">
					<span class="title"> <i>{{index+1}}</i> {{item.tag}}</span>
					<i class="arrow"></i>
				</div>
				<div class="detail" v-show="item.tab" v-html="item.desc">    </div>
			</div>
			
		</div>

	</div> 
</template>

<script>  
export default {
	name: 'accountQue',
  	data () {
	    return { 
	    	show:false,
	    	conList:[
	    		{
	    			tag:"如何开立模拟账户？",
	    			tab:false,
	    			desc:" 投资者可通过两种方式开立免费模拟账户：<br>1. 在金盛贵金属官方网站www.golday.com，点击“开立模拟账户”按钮，填写相关资料，点击“确认注册”按钮，系统将会发送模拟账户至您的手机中。<br>2.可直接联系在线客服人员获取免费模拟账号。"
	    		}, {
	    			tag:"模拟与真实账户的区别？",
	    			tab:false,
	    			desc:" 模拟账号和真实账号使用同一个交易平台。他们的数据来源和操作界面完全相同，投资者可通过使用模拟账户熟悉交易平台各项功能。模拟账户是虚拟资金，有效期为60天，若模拟额度不足或超过有效期，投资者可以随时在网页上再次申请。真实账户是永久有效的，投资者需使用真实金额进行交易。"
	    		},{
	    			tag:"真实交易账户有没有规定最低存款额？ ",
	    			tab:false,
	    			desc:" 在香港金盛贵金属有限公司办理开户不收取任何手续费，开户最低存款金额50美元，开户资金可全数用于投资交易。"
	    		},{
	    			tag:"如何开立真实账户？ ",
	    			tab:false,
	    			desc:" 1.网上开户：客户可登陆金盛贵金属官方网站点击“开立真实账户”，填写个人资料，点击“确认注册”即可完成开户。系统会自动发送交易账户和密码至客户开户时的手机号码和客户填报之电子邮箱中，客户需要进行注资激活交易账户才能进行真实交易。<br>2.亲身开户：客户可先联络客服预约时间，于约定时间携带身份证、银行账户证明及地址证明，亲临本公司办事处办理。完成后客户可以到银行存款，并需要把存款单据电邮、传真或邮寄方式发送到公司。收到资料后，我们会马上为您开通真实账户，本公司地址为香港九龙尖沙咀广东道9号海港城港威大厦第六座1605-06 室。 "
	    		},{
	    			tag:" 开户后需提交绑定个人资料？ ",
	    			tab:false,
	    			desc:" 为了保障客户的资金安全，投资者在开户成功后需要提交身份证和取款银行卡复印件(或扫描件、数码相片) 上传至“客户中心”进行绑定，或联系客服进行办理绑定业务。通过网站对话系统、QQ或电邮均可提交，建议发送成功后及时通知客服为您确认资料。 "
	    		},{
	    			tag:" 如果客户忘记或遗失自己的账户密码应该怎么办？  ",
	    			tab:false,
	    			desc:"可致电或在线联系我们客服，与客服核对个人资料后，我们会在5-10分钟内以手机短信或电邮的方式通知新的初始密码。为了保障客户的资金安全，请登录后马上修改密码。"
	    		},{
	    			tag:"使用什么货币进行交易？",
	    			tab:false,
	    			desc:"我们账户的指定交易货币为美元，若客户以人民币存取款项，均参考本公司公布的市场汇率作兑换。汇率价格公平公开，本公司绝不收取任何费用。"
	    		},{
	    			tag:"  如何注资？",
	    			tab:false,
	    			desc:" 您可以使用已开通网银的银行卡，在金盛页面进行自助注资，10分钟到账，手续费全免。也可以通过银行柜台办理跨境汇款，到账时间大约为1-3个银行工作日，银行可能会向您收取因汇款所产生的手续费。无论通过何种方式注资，我们都会在收到款项后10分钟完成办理。"
	    		},{
	    			tag:" 可以存入哪种货币的存款？ ",
	    			tab:false,
	    			desc:"您可以存入美元，人民币或港币存款。存款没有金额限制。"
	    		},{
	    			tag:" 取款货币会是什么？ ",
	    			tab:false,
	    			desc:"如客户要取款，我们会从账户中提取要求的美元款项，转账至客户的登记银行账户。如客户取款账户是人民币账户，均参考公司公布的市场汇率作兑换。如客户取款账户是港币账户，均使用香港恒生银行每星期日公布的市场汇率作兑换。保证汇率价格公平公开，不收取买任何费用或买卖差价，绝不取巧。 "
	    		} 
	    	]
	    }
  	} 
}
</script> 
 


