<template>
	<div class="deal">   
		<div class="box">
			<div class="group" v-for="(item,index) in conList" :key="index">
				<div class="flex headline" @click="item.tab = !item.tab">
					<span class="title"> <i>{{index+1}}</i> {{item.tag}}</span>
					<i class="arrow"></i>
				</div>
				<div class="detail" v-show="item.tab" v-html="item.desc">    </div>
			</div>
			
		</div>

	</div> 
</template>

<script>  
export default {
	name: 'deal',
  	data () {
	    return { 
	    	show:false,
	    	conList:[
	    		{
	    			tag:" 可交易手数为多少？?",
	    			tab:false,
	    			desc:"交易伦敦金/银，MT4交易平台单笔交易的最小手数是0.05手，单笔交易的最大手数是20手；MF交易平台单笔交易的最小手数是0.1手，单笔交易的最大手数是10手，可持有的总手数不限，所以交易手数并没有上限。"
	    		}, {
	    			tag:" 什么是强制平仓？",
	    			tab:false,
	    			desc:"当客户账户的预付款比例达到30%时，系统会从亏损最多的交易单开始强制平仓，直至预付款比例回到30%以上。当价格波动大出现跳空时，有可能跳过30%而在低于30%的预付款比例强制平仓或导致余额出现负数。 注：周末及特殊时段“维持保证金”强制平仓比例为60%。  "
	    		},{
	    			tag:"可交易手数为多少？",
	    			tab:false,
	    			desc:"交易伦敦金/银，MT4交易平台单笔交易的最小手数是0.05手，单笔交易的最大手数是20手；MF交易平台单笔交易的最小手数是0.1手，单笔交易的最大手数是10手，可持有的总手数不限，所以交易手数并没有上限。 "
	    		},{
	    			tag:"  贵金属产品的交易时间？",
	    			tab:false,
	    			desc:" 北京时间： 周一上午7:00至周六凌晨3:00(夏令时)  周一上午7:00至周六凌晨4:00(冬令时)  周末及国际节假日休市。 "
	    		}   
	    	]
	    }
  	} 
}
</script> 
 


