<template>
	<div class="topic"> 
		<div class="container" v-for="(item,index) in discountList" :key="index"> 
			<img class="discountImg" :src="item.src" alt="最新优惠">
			<p class="disName">{{item.title}} </p>
			<p class="disDesc">{{item.desc}}</p> 
		</div>
	</div> 
</template>

<script>  
import img from '../customer/img/customer_42.jpg'
export default {
	name: 'topic',
	data () {
	    return { 
	    	discountList:[
	    		{		
	    			src:img,
	    			title:'盛夏捞金，决胜美联储',
	    			desc:'美联储议息会议也称FOMC会议，会议主要内容为通过举行讨论会议投票决定联邦基金利率。利率的升级反应经济的好坏。美联储按惯例每年举行八次会议，每次会议一般持续一到两天，主要任务是决定美国货币政策。 '
	    		},
	    		{
	    			src:img,
	    			title:'精准布局非农行情，尽在掌中',
	    			desc:'非农数据，是指非农业就业人数、就业率与失业率这三个数值。这是由美国劳工部劳动统计局发布的反映美国制造业和服务业就业状况的数据指标。它反映了美国经济的兴衰，极大地影响货币市场的美元价值。 '
	    		},
	    		{
	    			src:img,
	    			title:'日银决议金市影响日本央行决议若释放鸽派信号 ',
	    			desc:'欧洲央行：政策利率仍将维持低位，直至QE计划结束“很久之后”；QE将持续到通胀路径出现可持续的调整；预计关键利率将在较长的时间内维持现有水平，并超过净资产购买计划的时间 '
	    		},
	    	]
	    }
  	} 
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped> 
	p{
		font-size:.35rem;
		line-height:.55rem;
	}
	.discountImg{
		width:100%; 
	}
	.disName{
		font-size: 0.3rem; 
		color: #333333; 
		line-height:.55rem;

	}
	.disDesc{
		font-size: 0.26rem;
		line-height:.4rem;
		text-indent:2em;
	}
</style>



