<template>
	<div class="wrapper">
		<transition-group tag="ul" name="slide"  class="swiper">
			<li class="slide" v-for="(item,index) in imgList" :key="index" v-if="index == mark">
				<img :src="item">
			</li> 
		</transition-group>
		<div class="bars">
			<span class="bar" :class="{'active':mark == index}" v-for="(item,index) in imgList" :key="index"></span>
		</div>
	</div> 
</template>

<script>
import index1 from '../img/index1.jpg'
import index2 from '../img/index2.jpg'
import index3 from '../img/index3.jpg'
export default {
	name: 'wrapper',
	data () {
	    return {
	    	mark:0,
	    	imgList:[index1,index1,index1]
	    }
    },
    created(){
    	this.autoPlay();
    },
    methods:{
    	autoPlay(){
    		setInterval(()=>{
    			this.mark++;
    			if(this.mark == this.imgList.length){
    				this.mark = 0;
    			}
    		},3000);
    	}
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.wrapper,.swiper{
		width:100%;
		height:6.8rem;
		position:relative;
	}
	.slide{
		position:absolute;
	}
	.slide,.slide img{
		width:100%;
		height:6.8rem;
	}
	.bars{
		width:100%;
		text-align: center;	
		position:absolute;
		bottom:.2rem;
	}
	.bar{
		width:.56rem;
		height:.05rem;
		background:#fff;
		margin:0 .1rem;
	}
	.bar.active{
		background:#ff5500;
	}
	/* 轮播过渡 */
	.slide-enter-active {
	    transform: translateX(0);
	    transition: transform 1.5s ease;
	}
	.slide-leave-active {
	    transform: translateX(-100%);
	    transition: transform 1.5s ease;
	}
	.slide-enter {
	    transform: translateX(100%);
	}
	.slide-leave {
	    transform: translateX(0);
	}
</style>



