<template>
	<div class="investQue"> 
		<div class="container" v-for="(item,index) in basicList" :key="index" v-if="show"> 
			<p class="conTitle">{{item.title}}</p>
			<p class="conText">{{item.desc}}</p>
		</div> 

		<div class="lookMore"><span class="morePlus"> + </span> 查看更多</div>
	</div> 
</template>

<script>  
export default {
	name: 'investQue',
	data () {
	    return { 
	    	show:true, 
	    	basicList:[
	    		{
	    			title:'111111炒黄金如何防止爆仓',
	    			desc:'多人在炒黄金的过程中，都面临过几乎爆仓的情况。爆仓，对投资者而言是十分严重的情况，不但会造成投资者大量资金的亏损…'
	    		},
	    		{
	    			title:'现货黄金如何根据K线判断走势',
	    			desc:'K线图是现货黄金技术分析盘面上的价格形态，它以大大小小不同的柱子条表示，下面我们就K线柱子图的形成，以及K线的技术形态，向各位炒黄金入门者介绍…'
	    		},
	    		{
	    			title:'K线图上影线、下影线的含义',
	    			desc:' 炒国际现货黄金的技术盘面走势图中，我们看到相同时间单位内形成的一根根现货黄金K线，按不同的时间周期，分成了黄金日K线图、周K线图、月K线……'
	    		},
	    	] 
	    }
  	}, 
}
</script>

 
 



