	    			desc:"下载完成后请按照以下步骤进行安装： 电脑java支援程式下载完成后，双击打开支援程式，进行安装。 <br>（1）MF交易平台安装双击金盛贵金属安装图标；<br> （2）选择语言，点击“OK”，进行“下一步”； <br>（3）选择安装路径，点击“下一步”；<br> （4）点击“安装”，开始安装交易平台，点击“完成”安装成功。"
	    		},{
	    			tag:" MT4交易平台优势? ",
	    			tab:false,
	    			desc:"我们为个人用户提供MT4交易平台进行网上电子交易。集专业与简易操作于一身的MT4交易平台广受国际机构、投资者及专业个人投资者所选用，是目前全球使用范围最广的交易平台。 <br>它综合了行情图表、技术分析、下单交易三大功能，功能俱全，操作简单。客户可以在交易平台进行下单、平仓、挂单、止损/获利、查看报价及分析数据等不同操作。MT4占用电脑资源少，运行速度快，还可以下载历史资料和图表，备受国际投资公司和投资者的青睐。"
	    		},{
	    			tag:"如何下载MT4交易平台？",
