<template>
	<div class="discount">
		<app-subHeader :subTitle="subTitle"></app-subHeader>
		<div class="container">
			<h2 class="topTitle mb30"> <i></i>  平台公告 <i></i> </h2>
			<div class="flex notice" v-for="(item,index) in noticeList" :key="index">
