	    	noticeList:[
	    		{
	    			title:'关于端午节假期暂停取款服务通知',
	    			time:'2018-06-14'
	    		},
	    		{
	    			title:'关于端午节假期暂停取款服务通知',
	    			time:'2018-06-14'
	    		},
	    		{
	    			title:'关于端午节假期暂停取款服务通知',
	    			time:'2018-06-14'
	    		},
	    	]
	    }
  	},
  	components:{ 
  		"app-subHeader":subHeader,
  	},
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.container{
		margin-bottom:.3rem; 
		padding:0 .3rem .3rem ; 
	}  
	.notice{
		width:100%;
		height:.84rem;
		line-height:.84rem;
		border-top:0.01rem solid #e8e8e8;
	}
	.notice span{
		font-size: 0.26rem; 
		color: #333333;
	} 
	.mb30{
		margin-bottom:.3rem;
	}
</style>



